* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.controlBtn {
  justify-content: center;
  align-items: center;
  margin: 100px;
  display: flex;
}

button {
  cursor: pointer;
  background-color: #63e9b1;
  border: .5px solid rgba(0, 0, 0, .35);
  border-radius: 4px;
  margin-left: 20px;
  padding: 8px;
  font-size: 22px;
  font-weight: 500;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

button:hover, button:focus {
  background-color: #57cccc;
  border-radius: 5px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

a:hover {
  text-shadow: 0 4px 4px rgba(0, 0, 0, .15);
  color: #8b008b;
  font-weight: 500;
}

.notice {
  letter-spacing: .03em;
  text-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
  font-family: Raleway;
  font-size: 19px;
  font-style: normal;
  line-height: 1.19;
}

.timer, .menu {
  justify-content: center;
  padding-top: 20px;
  display: flex;
}

.field {
  flex-direction: column;
  padding: 0 15px;
  display: flex;
}

input {
  height: 50px;
  border: 1px solid #184993;
  border-radius: 5px;
  padding-left: 20px;
  font-size: 21px;
}

.value {
  text-align: center;
  font-size: 30px;
  font-weight: 500;
}

.label {
  font-size: 23px;
}

